.App {
  width: 100%;
}
#root {
  width: 100%;
  display: flex;
  justify-content: center;
}
html {
  background-color: rgba(168, 168, 197, 0.144);
  display: flex;
  justify-content: center;
  overflow: hidden;
}
body {
  background-color: rgba(168, 168, 197, 0.144);
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0;
  width: 100vw;
}
#my-carousel {
margin-top: 15px;
 
}

.slide-container {
  background-color: rgba(168, 168, 197, 0.144);
}
.slick-prev {
  left: 8vw;
  z-index: 1;
}
.slick-next {
  right: 8vw;
}

.slick-track {
  margin: 0;
  padding: 0;
  width: 100vw;
}
